import banner from '../components/banner.vue'
import uniPopup from "../components/uni-popup/uni-popup.vue"
export default {
  components: {
    banner: banner,
    uniPopup: uniPopup
  },
  data () {
    return {
      swiperList: ['', ''],
      detail: {
        name: 'MOBY任天堂switch保护套ns保护壳nintendo游戏机外壳主机手柄一体s保护壳nintendo游戏机外壳主机手柄一体s保护壳nintendo游戏机外壳主机手柄一体',
        price: 70000,
        sell_point: '包邮'
      },
      specData: {
        spec_attr: [{
          group_name: '颜色',
          spec_items: [{
            item_id: 0,
            spec_value: '红'
          }, {
            item_id: 1,
            spec_value: '黄'
          }, {
            item_id: 2,
            spec_value: '蓝'
          }]
        }, {
          group_name: '尺寸',
          spec_items: [{
            item_id: 0,
            spec_value: '小'
          }, {
            item_id: 1,
            spec_value: '中'
          }, {
            item_id: 2,
            spec_value: '大'
          }]
        }]
      },
      select_arr: [0, 0],             // 选中的规格id值
      goods_num: 1,                   // 兑换的数量
    }
  },
  methods: {
    // 确认订单
    goOrder() {
      uni.navigateTo({
        url: 'order/orderSure'
      })
    },
    // 打开弹窗
    open(){
      // 需要在 popup 组件，指定 ref 为 popup
      this.$refs.popup.open()
    },
    // 商品规格
    selectNorm(shu, li, num) {
      let that = this
      console.log(shu, li , num)
      that.select_arr[shu] = li.item_id
      let arr = []
      let arr2 = []
      
      // 选中的规格id数组
      that.select_arr.map((item, index) => {
        if(index === shu) {
          arr.push(li.item_id)
        } else {
          arr.push(item)
        }
      })
      that.select_arr = arr
      
      // 选中的名称数组
      that.select_name.map((item, index) => {
        if(index === shu) {
          arr2.push(li.spec_value)
        } else {
          arr2.push(item)
        }
      })
      that.select_name = arr2
      
      console.log('选中的规格id', arr, '选中的规格名称',  arr2)
      
      let id = that.select_arr.join('_')
      
      // 图片及价钱和库存
      let obj = that.goods
      that.specData.spec_list.map((item, index) => {
        if(item.spec_sku_id === id) {
          obj.goods_price= item.form.goods_price
          obj.stock_num= item.form.stock_num
          if(item.form.image_path) {
            obj.image_path = item.form.image_path
          }
        }
      })
      that.goods = obj
    },
    // 设置商品数量
    controlNum(type) {
      if(type === '+') {
        // if(this.goods_num >= this.goods.stock_num) {
          uni.showToast({
            title: '库存不足',
            icon: 'none'
          })
          return
        // }
        this.goods_num += 1
      }
      else if(type === '-') {
        if(this.goods_num > 1) {
          this.goods_num -= 1
          return
        }
      }
    }
  }
}
